:root {
  --clr-accent: hsl(176 50% 47%);
  --clr-accent-dark: hsl(176 72% 28%);
  --clr-neutral-100: hsl(0 0% 100%);
  --clr-neutral-200: hsl(0 0% 98%);
  --clr-neutral-500: hsl(0 0% 48%);
  --clr-neutral-900: hsl(0 100% 0%);
}

:root {
  --ff-primary: "Commissioner", sans-serif;
  --fs-400: 0.625rem;
  --fs-600: 1.25rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: var(--ff-primary);
  font-size: var(--fs-400);
  line-height: 1.7;
  color: var(--clr-neutral-500);
  padding-bottom: 2rem;
  background-color: var(--clr-neutral-200);
}

button {
  cursor: pointer;
  font: inherit;
  color: inherit;
  border: none;
}

img {
  display: block;
  max-width: 100%;
}

.header {
  min-height: 18.75rem;
  background-size: cover;
  background-image: url("./images/image-hero-mobile.jpg");
}
@media (min-width: 48em) {
  .header {
    min-height: 25rem;
    background-image: url(./images/image-hero-desktop.jpg);
  }
}

.intro-section,
.stats-section {
  text-align: center;
}

.intro-section {
  position: relative;
}
.intro-section .logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stats {
  align-items: center;
  flex-direction: column;
}
.stats .stat {
  --gap: 1rem;
}
.stats .stat:not(:last-child) {
  padding-bottom: 1.375rem;
  border-bottom: 1px solid var(--clr-neutral-500);
}
@media (min-width: 48em) {
  .stats .stat:not(:last-child) {
    padding-bottom: 0;
    border-bottom: none;
    padding-right: 4rem;
    border-right: 1px solid var(--clr-neutral-500);
  }
}
@media (min-width: 48em) {
  .stats {
    flex-direction: row;
  }
  .stats .stat:not(:first-child) {
    padding-left: 2rem;
  }
}

.progress-bar {
  width: 100%;
  height: 0.75rem;
  margin-top: 2rem;
  border-radius: 2em;
  background-color: var(--clr-neutral-200);
  overflow: hidden;
}
.progress-bar .progress {
  width: 75%;
  height: 100%;
  background-color: var(--clr-accent);
}

.product-cards {
  margin-top: 2rem;
}

.attribution {
  font-size: 0.6875rem;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

h1,
h2,
h3 {
  line-height: 1.1;
  color: var(--clr-neutral-900);
}

a,
a:visited,
a:active {
  color: inherit;
  text-decoration: none;
}

.title {
  font-size: var(--fs-600);
}

.desc {
  margin-top: 1.25rem;
}

.stat__figure {
  font-size: 1.875rem;
  font-weight: 700;
  line-height: 1;
  color: var(--clr-neutral-900);
}

.card .pledge {
  font-size: 1rem;
  color: var(--clr-accent);
}

.stock__amount {
  font-size: 2rem;
  font-weight: 700;
  color: var(--clr-neutral-900);
}

.modal__title {
  font-size: 1.375rem;
}

.modal__desc {
  margin-block: 1rem 2rem;
}

.modal__label {
  font-size: 1.125rem;
  font-weight: 700;
  color: var(--clr-neutral-900);
}

.container {
  width: min(100% - 1.5rem, 45rem);
  margin: -55px auto 0;
}

.flex {
  gap: var(--gap, 1.5rem);
  display: flex;
}
.flex--column {
  flex-direction: column;
}
.flex--wrap {
  flex-wrap: wrap;
}

.card {
  padding: 3.125rem 2rem 2.5rem;
  border-radius: 0.625rem;
  background-color: var(--clr-neutral-100);
  position: relative;
}
.card p {
  margin-top: 1rem;
}
.card__header {
  align-items: center;
  justify-content: space-between;
}
.card__title {
  font-size: 1.375rem;
}
.card__footer {
  --gap: 0;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.5625rem;
}
.card--product, .card--modal {
  padding: 1.5rem;
  border: 1px solid var(--clr-neutral-500);
}
.card--product .stock, .card--modal .stock {
  --gap: 0.625rem;
  align-items: center;
}
.card--product .card__header,
.card--product .card__footer {
  --gap: 1rem;
  flex-direction: column;
  align-items: flex-start;
}
@media (min-width: 48em) {
  .card--product .card__header,
.card--product .card__footer {
    flex-direction: row;
    align-items: center;
  }
}
.card--product .card__header {
  --gap: 0.375rem;
}
.card--product:last-child {
  opacity: 0.65;
}
.card--modal hr {
  border: none;
  transition: all 300ms linear;
}
.card--modal .card__header {
  justify-content: flex-start;
}
.card--modal .card__footer {
  text-align: center;
  height: 0;
  opacity: 0;
  margin-top: 0;
  flex-direction: column;
  overflow: hidden;
  transition: all 300ms linear;
}
.card--modal .card__footer.active {
  height: auto;
  opacity: 1;
  margin-top: 1.56rem;
}
.card--modal .card__footer > .flex {
  --gap: 0.75rem;
  margin-top: 1rem;
}
@media (min-width: 48em) {
  .card--modal .card__footer > .flex {
    margin-top: 0;
  }
}
@media (min-width: 48em) {
  .card--modal .card__footer {
    text-align: start;
    flex-direction: row;
  }
}
.card--modal[data-selected=true] {
  border: 2px solid var(--clr-accent);
}
.card--modal[data-selected=true] hr {
  margin-top: 1.56rem;
  border-top: 1px solid var(--clr-neutral-200);
}

.btn {
  font-weight: 500;
  color: var(--clr-neutral-100);
  padding: 1rem 3rem;
  border-radius: 3em;
  background-color: var(--clr-accent);
  position: relative;
  transition: background-color 175ms ease-out;
}
.btn:hover, .btn:focus {
  background-color: var(--clr-accent-dark);
}
.btn--bookmark {
  --gap: 1rem;
  padding: 0;
  background-color: var(--clr-neutral-200);
  align-items: center;
}
.btn--bookmark .icon {
  width: 3.4375rem;
  aspect-ratio: 1;
  border-radius: 50%;
}
.btn--bookmark .text {
  color: var(--clr-neutral-500);
  padding-right: 1.5rem;
  display: none;
}
@media (min-width: 48em) {
  .btn--bookmark .text {
    display: inline-block;
  }
}
.btn--bookmark[data-bookmarked=true] {
  color: var(--clr-accent-dark);
}
.btn--bookmark[data-bookmarked=true] .icon circle {
  fill: var(--clr-accent-dark);
}
.btn--bookmark[data-bookmarked=true] .icon path {
  fill: var(--clr-neutral-100);
}
.btn--bookmark[data-bookmarked=true] .text {
  color: inherit;
}
.btn--bookmark:hover, .btn--bookmark:focus {
  background-color: var(--clr-neutral-200);
}
.btn--product {
  padding: 0.875rem 2.25rem;
}
.btn--disabled {
  cursor: not-allowed;
  background-color: var(--clr-neutral-500);
}
.btn--disabled:hover, .btn--disabled:focus {
  background-color: var(--clr-neutral-500);
}
.btn--submit {
  padding: 0.75rem 1.35rem;
}

.nav__toggle {
  gap: 4px;
  width: 1rem;
  aspect-ratio: 1;
  background-color: transparent;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
.nav__toggle .line {
  width: 100%;
  height: 3px;
  background-color: var(--clr-neutral-100);
  display: block;
  transition: transform 200ms linear;
}
.nav__toggle[aria-expanded=true] .line-1 {
  transform: translate(0, 6.5px) rotate(45deg);
}
.nav__toggle[aria-expanded=true] .line-2 {
  transform: scale(0);
}
.nav__toggle[aria-expanded=true] .line-3 {
  transform: translate(0, -6.5px) rotate(-45deg);
}

.nav {
  color: var(--clr-neutral-100);
  width: min(100% - 3rem, 69rem);
  margin-inline: auto;
  padding-block: 2rem;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.nav__list {
  --gap: 0;
  color: var(--clr-neutral-900);
  list-style: none;
  width: 100%;
  border-radius: 0.625rem;
  background-color: var(--clr-neutral-100);
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.15);
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 5;
  transform: translateY(-300%);
  transition: transform 300ms ease-in-out;
}
.nav__list-item {
  font-weight: 500;
  padding: 1.5rem;
}
.nav__list-item:not(:last-child) {
  border-bottom: 1px solid var(--clr-neutral-200);
}
@media (min-width: 48em) {
  .nav__list {
    --gap: 1.5rem;
    color: var(--clr-neutral-100);
    width: auto;
    border-radius: none;
    background-color: transparent;
    box-shadow: none;
    flex-direction: row;
    position: static;
    transform: translateY(0);
  }
  .nav__list-item {
    font-weight: 400;
    padding: 0;
  }
  .nav__list-item:not(:last-child) {
    border: none;
  }
}
.nav__toggle[aria-expanded=true] ~ .nav__list {
  transform: translateY(0);
}
@media (min-width: 48em) {
  .nav__toggle {
    display: none;
  }
}

.modal {
  width: 100%;
  height: 100%;
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.35);
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  overflow: auto;
  opacity: 0;
  transform: translateY(-100%);
}
.modal[aria-expanded=true] {
  opacity: 1;
  transform: translateY(0);
}
.modal__content {
  width: 100%;
  max-width: 45rem;
  padding: 2.5rem 1.5rem;
  border-radius: 0.625rem;
  background-color: var(--clr-neutral-100);
  position: relative;
}
.modal__close-btn {
  background-color: transparent;
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
}
.modal .pledge-input {
  cursor: pointer;
  font-weight: 500;
  padding: 0.75rem 1.35rem;
  border-radius: 3em;
  border: 1px solid var(--clr-neutral-500);
}
.modal .pledge-input__currency {
  margin-right: 0.25rem;
}
.modal .pledge-input__amount:focus {
  outline: none;
}
.modal .pledge-input:hover, .modal .pledge-input:focus {
  border-color: var(--clr-accent);
}
.modal--success {
  text-align: center;
}
.modal--success .modal__content {
  max-width: 35rem;
}
.modal--success .modal__title {
  margin-top: 2rem;
}
.modal__icon {
  margin-inline: auto;
}
.modal__close-btn--success {
  font-weight: 500;
  color: var(--clr-neutral-100);
  padding: 1rem 3rem;
  border-radius: 3em;
  background-color: var(--clr-accent);
}
.modal__close-btn--success:hover, .modal__close-btn--success:focus {
  background-color: var(--clr-accent-dark);
}
@media (min-width: 48em) {
  .modal .stock {
    position: absolute;
    top: 0.875rem;
    right: 1.5rem;
  }
}